import React from 'react';
import Tilt from 'react-tilt';
import './Index.css';
import sam from './w4.jpg';
import sap from './w41.jpg';


const Index=({onRouteChange})=>{
	return(
		<div>
		
		<div id="page-wrap">
		<div className="Tilt br3 ma  " options={{ max : 25 }} style={{ height: 100, width: 1000 }} >
             <div className="Tilt-inner  "> <h1 className='bg-wite f1 tc'style={{paddingTop:'10px'}}>Trendy Collections</h1> </div>
          </div>
                   
                   <h5 >Best  Saree Collections</h5>
             </div>
        <div className="tc">
		     <input 
		      onClick={()=>onRouteChange('Card')}
		      className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer"
		      type="submit"
		      value="Saree Models ->"
		       />
		   </div>
<br /> 
           <div className="inline-flex ">
		   <div className="   br3   w-50 h-30 tc ">
		   <img src={sam} />
		   </div>

		    <div className="  br3   w-50 tc ">
		   <img src={sap} />
		    </div>

		    </div>

		    <br /><br /><br /><br />
		    <div className="container ">
		      <marquee  className="cs grow" behavior="scroll" direction="up">
 					Top grade <b>cotton handloom sarees</b> feel soft and is the most suitable fabric for the Indian air.
		           Our weavers and partners use the highest grade cottons to bring you the best handloom experience. 
		           Cotton made in pit looms and shuttle looms of India are also more airy yet strong compared to their machine woven counterparts.
		      </marquee>
		      

		      <marquee className="soft grow"  behavior="scroll" direction="up" >
		      	   <b>Soft silk saree</b> is a fusion of contemporary and conventional designs and can be worn with more ease and comfort every day. 
		           In fashion, comfort doesn't mean playing safe. 
		           It means you are given a choice to get creative with your taste.
		       </marquee>


		      <marquee className="ss grow" behavior="scroll" direction="up">
		           <b>Kanchipuram silk sarees</b> are woven from pure mulberry silk. 
		           While the silk belongs to South India, the pure gold and silver zari comes from Gujarat.
		           The silk thread that is used to weave the saree is dipped in rice water and sun-dried before
		           it is used in order to increase both, its thickness and stiffness.
		      </marquee>


		      </div>

		    
		  

          
		</div>
        );
}
export default Index;